import React from 'react';

const MovieDetailsWithoutTrailer = ( { id, status, loading, onDetailsToHomeButton, title, relDate, overview, voteCount, voteAvg, classTitle, onAddWatchList, addButtonDisabled, onRemoveWatchList, removeButtonDisabled, poster } ) => {
  return (
  	<div className="movieDetails">
		<div className="mDetailsError">
			<p className="mDetailsErrorStatus">{status}</p>
		</div>
		<div className="mListLoading">
    		<p className="mListLoadingMsg">{loading}</p>
    	</div>
		<div className="moreInfoToHome">
			<button className="moreInfoToHomeButton" onClick={onDetailsToHomeButton} >Home</button>
		</div>
		<div className="moreInfo">
			<div className="moreInfoPoster">
          		<img src={"https://image.tmdb.org/t/p/w500/"+poster} onError={(e)=>{ e.target.src="https://in.bmscdn.com/iedb/movies/images/website/poster/large/the-mighty-mimalayan-man-et00009785-24-03-2017-19-30-07.jpg"}} alt="Poster not available" width="300" height="500"/>
          	</div>
          	<div className="moreDetails">
              	<div className="otherDetails">
	              <p className="mDetailsTitle">{title}</p>
	              <p className="mDetailsRelDate">{relDate}</p>
	              <p className="mDetailsOverview">{overview}</p>
	              <p className="mDetailsVoteCount">Vote Count: {voteCount}</p>
	              <p className="mDetailsVoteAvg">Vote Avg: {voteAvg}</p>
	            </div>
	            <div className="extraDetails">
	              <button className={classTitle} onClick={() => onAddWatchList(id, title, relDate, poster, voteCount, voteAvg, overview)} disabled={ addButtonDisabled }>Add to Watchlist</button>
	              <button className="removeWatchList" onClick={() => onRemoveWatchList(id)} disabled={ removeButtonDisabled }>Remove From Watchlist</button>
	            </div>
	        </div>
	    </div>
	</div>
  );
};

export default MovieDetailsWithoutTrailer;